.widget {
    display:flex;
    flex: 1;
    padding: 10px;
    justify-content: space-between;
    -webkit-box-shadow: -2px 4px 10px 6px rgba(199,199,199,0.78); 
    box-shadow: -2px 4px 10px 6px rgba(199,199,199,0.78);
    border-radius: 10px;
    height: 100px ;
   
    .left, .right {
        display: flex;
        flex-direction: column;
        justify-content: space-between ;

        .title {
            font-weight: bold;
            font-size: 14px;
            color: gray;
        }

        .counter {
            font-size: 28px;
            font-weight: 300;
        }

        .link {
            width: max-content;
            font-size: 12px;
            border-bottom: 1px solid gray;
        }

        .percentage {
            display: flex;
            align-items: center;
            font-size: 14px;

            &.positive {
                color: green;
            }
            &.negative {
                color: red;
            }
        }

        .icon {
            font-size: 18px;
            padding: 5px;
            background-color: rgba(253, 233, 250, 0.474);
            border-radius: 5px;
            align-self: flex-end;
        }
    }
}